<template>
  <div>
      <br>
      <div class="rock" @click="runGame('rock')">Rock</div>
      <div class="paper" @click="runGame('paper')">Paper</div>
      <div class="scissors" @click="runGame('scissors')">Scissors</div>
  </div>
</template>

<script>
export default {
    data() {
        return {
            choices: ["rock", "paper", "scissors"],
            lastResult: "",
            history: [],
        }
    },
    methods: {
        runGame(choice) {
            let comChoice = Math.floor(Math.random() * 3) + 1;
            switch (comChoice) {
                case 1:
                    comChoice = "rock"
                    break;
                case 2:
                    comChoice = "paper";
                    break;
                case 3:
                    comChoice = "scissors"
                    break;
                default:
                    alert("COM CHOICE NOT RECOGNIZED: " + comChoice)
                    break; 
            }

            let result = "TIE!"

            switch (comChoice) {
                case choice:
                    break;
                case "rock":
                    if (choice == "paper") {
                        result = "WIN!"
                    }
                    else {
                        result = "LOSE!"
                    }
                    break;
                case "paper":
                    if (choice == "rock") {
                        result = "LOSE!"
                    }
                    else {
                        result = "WIN!"
                    }
                    break;
                case "scissors":
                    if (choice == "rock") {
                        result = "WIN!"
                    }
                    else {
                        result = "LOSE!"
                    }
                    break;
                default: 
                alert("SOMETHING WENT WRONG")
                break;
            }
                alert(choice + " vs " + comChoice + " : " + result)

            // if (choice == comChoice) {
            //     alert(choice + " vs " + comChoice + " = Tie")
            // }
            // else if (comChoice == "rock") {
            //     if (choice == paper) {
            //         alert("You win!")
            //     }
            //     else {
            //         alert("You lose!")
            //     }
            // }
            // else if (comChoice == "paper") {

            // }
            // alert(comChoice + " : " + choice);
        }
    },
    created() {

    }
}
</script>

