<template>
  <div id="dnd">
      <div id="dnd-title">{{title}}</div>
        <div class="dnd-sections">
            <div class="dnd-section-1">
                <div class="dnd-questions">

                    <div v-if="statsDone" class="dnd-stat-entry">
                        <button @click="randomizeStats()">Random</button>
                        <!-- <input type="text" v-for="stat in baseStats.stats" :key="stat.index" :placeholder="baseStats[baseStats.indexOf(stat)][0]" v-model="baseStats[baseStats.indexOf(stat)][2]"/>  -->
                        <input type="text" v-for="stat in characterSheet.stats" :key="stat[0]" :placeholder="stat[0]" v-model="characterSheet.stats[characterSheet.stats.indexOf(stat)][2]" />
                    </div>

                    <!-- <div class="race-select-titles">
                        <label v-show="currentRace != ''" for="race-select" class="race-label">Race</label>
                        <label v-show="currentClass != ''" for="class-select" class="class-label">Class</label>
                    </div> -->

                    <div class="race-class">
          
                        <select v-if="races.length != 0" name="race-select" id="race-select" class="selector" v-model="currentRace" @change="fetchRaceInfo">
                            <option value="" selected disabled hidden>Choose a race</option>
                            <option :value="race.index" v-for="race in races" :key="race.index">
                                {{race.name}}
                            </option>
                        </select> 

            
                        <select v-if="classes.length != 0" name="class-select" id="class-select" class="selector" v-model="currentClass" @change="fetchClassInfo">
                            <option value="" selected disabled hidden>Choose a class</option>
                            <option :value="dndclass.index" v-for="dndclass in classes" :key="dndclass.index">
                                {{dndclass.name}}
                            </option>
                        </select>

                    </div>
                    <div class="proficiency-choices" v-if="currentClass != ''">
                        <select name="proficiency-select" v-model="characterSheet.otherProficienciesAndLanguages.selectedProficiencies[0]" v-if="proficiencyChoices[1]">
                            <option value="" selected disabled>Select a proficiency</option>
                            <option :value="proficiency.name" v-for="proficiency in tempProficiencyChoices[1]" :disabled="characterSheet.otherProficienciesAndLanguages.selectedProficiencies.includes(proficiency.name)" :key="proficiency.name">{{proficiency.name}}</option>
                        </select>
                       <select name="proficiency-select" v-model="characterSheet.otherProficienciesAndLanguages.selectedProficiencies[1]" v-if="proficiencyChoices[0] > 1">
                            <option value="" selected disabled>Select a proficiency</option>
                            <option :value="proficiency.name" v-for="proficiency in tempProficiencyChoices[1]" :disabled="characterSheet.otherProficienciesAndLanguages.selectedProficiencies.includes(proficiency.name)" :key="proficiency.name">{{proficiency.name}}</option>
                        </select>
                       <select name="proficiency-select" v-model="characterSheet.otherProficienciesAndLanguages.selectedProficiencies[2]" v-if="proficiencyChoices[0] > 2">
                            <option value="" selected disabled>Select a proficiency</option>
                            <option :value="proficiency.name" v-for="proficiency in tempProficiencyChoices[1]" :disabled="characterSheet.otherProficienciesAndLanguages.selectedProficiencies.includes(proficiency.name)" :key="proficiency.name">{{proficiency.name}}</option>
                        </select>                        
                    </div>
                </div>
            </div>
            <!-- END OF dnd-section-1 -->

            <div class="dnd-section-2">
                <div class="dnd-section-2-title">
                    Stats and Bonuses
                </div>
                <div class="dnd-ability-bonuses">
                    <div v-if="currentRaceStats.ability_bonuses">
                   -- Bonuses --
                    </div>
                    <div v-for="bonus in currentRaceStats.ability_bonuses" :key="currentRaceStats.ability_bonuses.indexOf(bonus)" >{{bonus.ability_score.name}} +{{bonus.bonus}}</div>
                    <!-- <div v-for="bonus in currentClassStats.ability_bonuses" :key="currentClassStats.ability_bonuses.indexOf(bonus)">{{bonus.ability_score.name}} +{{bonus.bonus}}</div> -->
                    <br >
                    <div>
                    -- Base Stats --
                    </div>
                    <div v-for="stat in characterSheet.stats" :key="stat[0]">
                        {{stat[1]}}: {{stat[2]}}
                    </div>
                    <br />
                    <div>--- Other Stats ---</div>
                    <div>Speed: {{characterSheet.speed}}</div>
                    <div>HitDie: {{characterSheet.hitDie}}</div>
                    <br />
                    <div>--- Languages ---</div>
                    <div v-for="language in characterSheet.otherProficienciesAndLanguages.languages" :key="language.name">{{language.name}}</div>
                    <br />
                    <div>--- Proficiencies ---</div>
                    <div v-for="proficiency in characterSheet.otherProficienciesAndLanguages.proficiencies" :key="proficiency.index">{{proficiency.name}}</div>
                    <br />
                    <div>--- Skills ---</div>
                    <div v-for="proficiency in characterSheet.otherProficienciesAndLanguages.selectedProficiencies" :key="proficiency">{{proficiency}}</div>
                    <br />
                    <div>--- Saving Throws ---</div>
                    <div></div>
                </div>
            </div>
            <!-- END OF dnd-section-2 -->
            <div class="dnd-section-3">
                <div class="dnd-section-3-title">
                Character Sheet Preview
                </div>
                <img id="dnd-character-sheet" src="../../../public/dndCharacterSheet1.png" />
            </div>
        </div>    
  </div>
</template>

<script>
// import APICommunication from "../../APICommunication/APICommunication.js";
import axios from "axios";

export default {
    data() {
        return {
            baseURL: "https://www.dnd5eapi.co/api/",
            title: "D&D Character Builder",
            baseStats: [["STR","Strength", ""], ["DEX","Dexterity", ""], ["CON","Constitution", ""], ["INT", "Intelligence", ""], ["WIS", "Wisdom", ""], ["CHA", "Charisma", ""]],
            allStats: [["STR","Strength", ""], ["DEX","Dexterity", ""], ["CON","Constitution", ""], ["INT", "Intelligence", ""], ["WIS", "Wisdom", ""], ["CHA", "Charisma", ""]],
            races: [],
            currentRace: "",
            currentRaceStats: {},
            classes: [],
            currentClass: "",
            currentClassStats: {},
            statsDone: true,
            proficiencyChoices: [],
            tempProficiencyChoices: [],
            // This is the main character object. Choices made by the user will be updated here.
            fullCharacter: {
                name: "",
                age: 0,
                heightFt: 0,
                heightIn: 0,
                alignment: ["",""],
                baseStats: [],
                allStats: [],
                currentRace: "",
                currentClass: "",
                hitDie: 0,
                proficiencies: [],
                savingThrows: [],
                spells: [],
                equipment: [],
                multiClass: [],
                subClass: [],
            },
            characterSheet: {
                keyInfo: {
                    name: "",
                    class: "",
                    level: "",
                    background: "",
                    playerName: "",
                    race: "",
                    alignment: "",
                    experiencePoints: 0,
                },
                stats: [
                    // STR: ["strength", 0],
                    // DEX: ["dexterity", 0],
                    // CON: ["constitution", 0],
                    // INT: ["intelligence", 0],
                    // WIS: ["wisdom", 0],
                    // CHA: ["charisma", 0],
                    ["STR","Strength", ""],
                    ["DEX","Dexterity", ""],
                    ["CON","Constitution", ""],
                    ["INT", "Intelligence", ""],
                    ["WIS", "Wisdom", ""],
                    ["CHA", "Charisma", ""]
                ],
                inspiration: 0,
                proficiencyBonus: 0,
                savingThrows: {
                    strength: 0,
                    dexterity: 0,
                    constitution: 0,
                    intelligence: 0,
                    wisdom: 0,
                    charisma: 0,
                },
                skills: {
                    acrobatics: 0,
                    animalHandling: 0,
                    arcana: 0,
                    athletics: 0,
                    deception: 0,
                    history: 0,
                    insight: 0,
                    intimidation: 0,
                    investigation: 0,
                    medicine: 0,
                    nature: 0,
                    perception: 0,
                    performance: 0,
                    persuasion: 0,
                    religion: 0,
                    sleightOfHand: 0,
                    stealth: 0,
                    survival: 0,
                },
                perception: 0,
                otherProficienciesAndLanguages: {
                    languages: [],
                    proficiencies: [],
                    selectedProficiencies: ["","",""],
                },
                armorClass: 0,
                initiative: 0,
                speed: 0,
                hitPoints: {
                    currentHitPoints: 0,
                    temporaryHitPoints: 0,
                    totalHitPoints: 0,
                },
                hitDie: 0,
                deathSaves: {
                    successes: 0,
                    failures: 0,
                },
                //This will hold all possible attacks and spells in the attack object
                attacksAndSpellCasting: [],
                attack: {
                    name: "",
                    attackBonus: "",
                    damage: 0,
                    type: "",
                },
                equipment: {
                    copper: 0,
                    silver: 0,
                    etherium: 0,
                    gold: 0,
                    platinum: 0,

                },
                personality: {
                    //This array will hold strings
                    personalityTraits: [],
                    ideals: [],
                    bonds: [],
                    flaws: [],
                },
                //This array will likely also hold attacks and spells
                featuresAndTraits: [],
                

            },
        }
    },
    methods: {
        updateSecondaryChoices() {
            
            },
        //Second data calls
        fetchRaceInfo() {
            axios.get(this.baseURL + "races/" + this.currentRace).then(response => {
                console.log(response.data);
                // this.allStats.forEach(stat => {
                    //     if (stat[0] == response.data.index) {
                        //         stat[2] = 0;
                //         // stat[2] -= response.data.ability_bonuses.ability_score.bonus;
                //     }
                // }) 
                // this.currentRaceStats.index
                this.currentRaceStats = response.data
                this.updateBonuses("race");
                // this.allStats
            })
            .catch(ex => {
                console.log("Error: " + ex);
            })
        },
        fetchClassInfo() {
            axios.get(this.baseURL + "classes/" + this.currentClass).then(response => {
                console.log(response.data);
                this.currentClassStats = response.data;
                this.updateBonuses("class");
            })
        },
    //Reusable bonus calculator
    updateBonuses(type) {
        let currentBonuses = [];
        let currentStats = [];
        let currentIndex = 0;
        console.log(type)

        if(type == "race") {
            console.log(this.currentRaceStats);
            this.currentRaceStats.ability_bonuses.forEach((bonus) => {
                currentStats[currentIndex] = bonus.ability_score.name;
                currentBonuses[currentIndex] = bonus.bonus;
                currentIndex++;
                })

                this.characterSheet.speed = this.currentRaceStats.speed;
                
                this.characterSheet.otherProficienciesAndLanguages.languages = this.currentRaceStats.languages

            }

        if(type == "class") {
            this.characterSheet.otherProficienciesAndLanguages.selectedProficiencies = ["", "", ""]
            this.characterSheet.hitDie = this.currentClassStats.hit_die;
            this.characterSheet.otherProficienciesAndLanguages.proficiencies = this.currentClassStats.proficiencies;
            this.tempProficiencyChoices = [this.currentClassStats.proficiency_choices[0].choose, this.currentClassStats.proficiency_choices[0].from]
            this.proficiencyChoices = this.tempProficiencyChoices;
        }
        // currentStats.forEach((stat) => {
        //     this.allStats.forEach((allStat) => {
        //         if (stat == allStat[0]) {
        //             allStat[2] += currentBonuses[currentStats.indexOf(stat)]
        //         }
        //     })
        // })

    this.fullCharacter.allStats = this.allStats;

    },
    randomizeStats() {
        this.statsDone = false;
        console.log("randomizing!")
        for (let e = 0; e < 6; e++) {
            let diceResults = [0,0,0,0,0,0];
            for (let i = 0; i < 6; i++) {
                diceResults[i] = Math.floor(Math.random() * 6) + 1
            }
            diceResults = diceResults.sort((a,b) => b-a);
            this.characterSheet.stats[e][2] = (diceResults[0] + diceResults[1] + diceResults[2]).toString(); 
        }
        this.statsDone = true;
    },
},
    //First data calls
    created() {
        axios.get(this.baseURL + "races").then(response => {
            console.log("Races Received!");
            console.log(response.data);
            this.races = response.data.results;
        })

        axios.get(this.baseURL + "classes").then(response => {
            console.log("Classes Received!")
            console.log(response.data)
            this.classes = response.data.results;
        })
    },
}
</script>

<style>
#dnd {
    position: absolute;
    top: 28px;
    left: 0;
    background-image: url("../../../public/dndbackground.png");
    height: 100%;
    width: 100%;
    /* overflow: hidden; */
}

#dnd-title {
    font-size: 32px;
    margin: 20px auto;
    color: lightseagreen;
    /* background-color: rgb(50,50,50); */
    /* border-radius: 10px; */
    width: 18%;
}

.dnd-sections {
    display: flex;
}

.dnd-sections > div {
    width: 33%;
}

/* .dnd-section-1 {
    /* width: 33%;
} */

.dnd-section-2 {
    color: lightseagreen;
    background-color: rgba(0,0,0,.5);
    border-radius: 8px;
    text-align: left;
    margin: 0 10px;
    padding: 0 5px;
    
    /* text-decoration: underline; */
}

.dnd-section-2-title {
    text-decoration: underline;
    margin-bottom: 10px;
    text-align: center;
}

/* .dnd-section-3 { */
    /* color: lightseagreen; */
    /* width: 33%; */
    /* background-color: white; */
    
/* } */

.dnd-section-3-title {
    margin-bottom: 10px;
    color: lightseagreen;
}

#dnd-character-sheet {
    width: 90%;   
    border: 2px solid black;
}
/* .dnd */

/* .dnd-questions {
} */

.selector {
    margin: 10px;
    background-color: black;
    color: lightseagreen;
    border-radius: 5px;
    height: 46px;
    width: auto;
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    box-shadow: 10px 5px 5px rgb(50,50,50);
    border-bottom: 6px solid rgb(30, 30, 30);
    border-right: 4px solid rgb(20,20,20);
    border-top: 2px solid rgb(130, 130, 130);
    border-left: 2px solid rgb(150, 150, 150);
    padding-right: 10px;
    padding-bottom: 10px;
    padding: 2px 0 0 2px;
    outline: none;
    transition-property: box-shadow, transform;
    transition-duration: 0.2s;
}

.selector:hover {
    box-shadow:  15px 12px 10px rgb(50,50,50);
    transform: translate(3%, 3%) scale(1.02);
}

.race-select-titles {
    width: 100%;
}

.race-select-titles * {
    text-decoration: underline;
    font-weight: bold;
    padding: 5px;
    background-color: rgb(50,50,50);
    color: lightseagreen;
    border-radius: 20%;
}

.race-label {
    /* position: relative;
    right: 4%; */
    margin-right: 150px;
}

/* .class-label { */
    /* position: relative;
    left: 3.4%; */
/* } */

.dnd-stat-entry {
    /* background: black; */
    display: flex;
    justify-content: space-between;
    width: 57%;
    margin: auto;

}
.dnd-stat-entry input {
    width: 40px;
}

.proficiency-choices select {
    background-color: black;
    color: lightseagreen;
    font-weight: bold;
    height: 26px;
    width: 180px;
    margin: 10px;
    border-radius: 5px;
}

</style>