<template>
    <div id="header">
        <!-- <img id="header-logo" class="header-selectable" alt="Vue logo" src="../assets/logo.png">
        <div id="header-username" class="header-selectable" @click="openAccountSettings()">
                {{ username }}
        </div> -->
        <div id="menu-options">
        <div @click="showHome()" :class="{menuselected: this.currentView == 'Home'}">Home</div>
        <div @click="showAbout()" :class="{menuselected: this.currentView == 'About'}">About</div>
        </div>
        <div id="header-clock">
            {{ clock }}
        </div>
    </div>
</template>

<script>
import { eventBus } from "../main.js";

export default {
  data() {
    return {
        username: "l3rad",
        clock: "",
        currentView: "Home"
    };
  },
  methods: {
    showHome() {
      eventBus.$emit('showhome')
      this.currentView = "Home";
    },
    showAbout() {
      eventBus.$emit('showabout');
      this.currentView = "About"
    },
    //ABOVE ARE DEVELOPED FOR THIS APPLICATION
    openAccountSettings() {
        eventBus.$emit('clearModalData');
        this.$store.state.modalWindow = {
            headerText: this.username + " Account Settings",
            bodyText: "Testing body text"
        }
        this.modalOpen();

    },
      // General Utility Methods
    modalOpen() {
        this.$nextTick(() => {
            eventBus.$emit('openModalWindow');
        })
    },
    updateDateTimeDisplay() {
      // get a new date (locale machine date time)
        let currentDate = new Date();
      // get the date as a string
        let date = currentDate.toDateString();
      // get the time as a string
        let time = currentDate.toLocaleTimeString();
      // combine them into a single string
        this.clock = date + " - " + time;
    },
  },
  created() {
        this.updateDateTimeDisplay();
        setInterval(this.updateDateTimeDisplay, 1000);
  },
}
</script>

<style>

#header {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgb(50,50,50);
    color: white;
    width: 100%;
    align-items: center;
    height: 30px;
}

#header-logo {
    height: 20px;
    margin: 0 4px 2px 8px;
    border-radius: 4px;
}

#header-username {
    text-align: center;
    justify-content: center;
    margin: 0 4px 2px;
    border-radius: 4px;
    font-weight: bold;
}

#header-clock {
    color: lightseagreen;
    position: absolute;
    right: 10px;
}

.header-selectable {
    padding: 4px;

}

.header-selectable:hover {
    background-color: rgb(40,40,40);
}

#menu-options {
  display: flex;
  width: 100px;
  justify-content: space-between;
  margin-left: 5px;
}

#menu-options * {
  font-weight: bold;
}

#menu-options *:hover {
  color: rgb(40,40,40);
  background-color: white;
  border-radius: 5px;
  padding: 2px;
  margin: -2px;
  cursor: pointer;
  transition-property: background-color, color;
  transition-duration: .3s;
}

.menuselected {
  text-decoration: double underline;
}
</style>