<template>
  <div>
      <home v-if="currentView == 'home'" />
    <about v-if="currentView == 'about'" />
    <dn-d-character-sheet-main v-if="currentView == 'dnd'" />
  </div>
</template>

<script>
import { eventBus } from "../main.js";
import Home from "./Home.vue";
import About from "./About.vue";
import DnDCharacterSheetMain from "./DnDCharacterSheet/DndCharacterSheetMain.vue";

export default {
components: {
    Home,
    About,
    DnDCharacterSheetMain,
},
data() {
    return {
        currentView: "home",
    }
},
methods: {
},
created() {
   eventBus.$on('showabout', () => {
       this.currentView = "about";
   }) 
   eventBus.$on('showhome', () => {
       this.currentView = 'home';
   })
   eventBus.$on('dnd', () => {
       this.currentView = 'dnd'
   })
}
}
</script>

