var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',[_vm._m(0),_c('div',{staticClass:"about-text"},[_c('div',{staticClass:"about-text-title"},[_vm._v("About Me")]),_c('div',[_vm._v(" Even though I have been coding for "+_vm._s(_vm.timeCoding)+" now, with the experiences and skills I have obtained throughout my career, it feels like I have been developing my skills as a programmer the entire time. ")]),_c('br'),_c('div',[_vm._v(" Out of highschool, I had only worked two jobs, one at McDonalds, and one at a place called DNite Sign Company. I wasn't sure what I wanted to do, so I started off at college taking a bunch of general education classes, ones that were required for almost any degree. I managed to obtain my Associates of Arts degree at Ohio State University, but I wanted to take some time off to figure out what I should do for my Bachelors degree. ")]),_c('br'),_c('div',[_vm._v(" However, I couldn't just sit around and do nothing, so a close friend of mine who had been in the same situation as me and I decided together to take a year off and try to flip a house with. We managed to pull together an investment and put a down payment on a house out in Newark. During the flip, we redid an entire bathroom, adding a shower and new flooring, added an entirely new bathroom, removed an entire wall, and redid the entire kitchen from scratch. It was a massive project, meaning my friend and I had to really plan out the work and expenses. It was a lot of work and taught me how to manage a large project by breaking it down into much smaller tasks, then completing each task systematically. ")]),_c('br'),_c('div',[_vm._v(" After flipping the house, I decided to try taking classes again while working at a math tutoring place called Mathnasium. Here I was able to tutor children of all ages, from pre-k, to senior in high school, on how to work out complex logical problems. This taught me how to break down complicated logic in subjects like algebra, calculus, and statistics into smaller chunks that were much easier to solve on their own. These strong mathematical building blocks have helped me to better flow through the logic that programs so often use to complete tasks efficiently. ")]),_c('br'),_c('div',[_vm._v(" After leaving Mathnasium, I found myself a position as an executive assistant at DaNite Sign Company. Here I worked for a while in Accounts Receivable, before revamping their entire file organizational system. They used a program called Green Folders, which allowed heavy customization of projects into so called \"folders\". Each job for the sign company would have its own folder, and it was my job to make sure these folders had efficient work-flows and usability. I would go around the office interviewing employees for each aspect of a job, from sales to construction and from service to collection, and custom built a folder design for each that would best fit their work-flow. I used integrated fields that would automatically fill out information across the project when one value was entered. For example, if the project manager tab required an install section, the same section used in the sign construction tab would be replicated on the project managers tab for easy access. This was my first experience taking user experiences and using them to built a custom layout that solved any gripes they may have had in the past. ")]),_c('br'),_c('div',[_vm._v(" After this job, I knew what I wanted to do. My older brother, Josh, actually pushed me to try a programming camp some of his peers had gone through. He recommended I try some quick courses of freecodecamp.com, then looking at a place called Tech Elevator. After coding for a bit on freecodecamp, I fell in love with programming. I quickly joined a the programming camp and had a blast learning everything I could about .NET development. ")]),_c('br'),_c('div',[_vm._v(" Shortly after graduating from the camp, I was hired on at Intellitech Corporation. Here, I was trained on their Jail Management System, which is software used by jails to manage everything, from cell assignments, to meals, billing, incidents, and even visitation to name a few. After around 6 months, I was handed off the entirety of the jail application to handle on my own. On top of this, I was developing web based solutions that would connect to the same database as the jail software to organize and display general information about the jail, or developing APIs that would collect and send the information to external organizations used by the jail. ")]),_c('br'),_c('div',[_vm._v(" I built this website from scratch using a Vue.js framework in pure HTML, JavaScript and CSS. This website exists as a small portfolio of sorts and is meant to display some of the skills I have managed to obtain during my time as a software engineer so far. ")])])])}
