<template>
  <div>
      <div class="title">Welcome to Brad's Website!</div>
      <br>
      <div class="body">
          From here, you can navigate to any of my web-based projects
      </div>
      <div class="project-directory">
          <div v-for="project in projects" :key="project.title" 
          @mouseover="mouseOver(project.title)" @mouseout="mouseOut(project.title)">
              <!-- <div :class="{directoryenvelope: hoveringDirectory == project.title}"> -->
              <div class="directory-header" :class="{directoryHeaderHovered: hoveringDirectory == project.title}" @click="openProject(project.key)">
                <div>{{project.title}}</div>
                <br>
                <div v-if="hoveringDirectory == project.title">{{project.longDescription}}</div>
                <div v-else>{{project.shortDescription}}</div>
                <rock-paper-scissors @mouseover="hoveringDirectory = project.title" v-show="hoveringDirectory == 'Rock Paper Scissors' && hoveringDirectory == project.title" />

              </div>
              <!-- </div> -->
          </div>
      </div>
  </div>
</template>

<script>

import RockPaperScissors from "./RockPaperScissors/RockPaperScissors.vue";
import {eventBus} from "../main.js"

export default {
    components: {
        RockPaperScissors,
    },
    data() {
        return {
            projects: this.$store.state.projects,
            hoveringDirectory: '',
        }
    },
    methods: {
        mouseOver(project) {
            this.hoveringDirectory = project;
        },
        mouseOut(project) {
            if (this.hoveringDirectory == project) {
                this.hoveringDirectory = "";
            }
            // setTimeout(() => {

            // }, 150)
        },
        openProject(key) {
            eventBus.$emit(key);
        },
    },
}
</script>

