    box-shadow: 10px 5px 5px rgba(50,50,50, 0.7);
    /* border-top: 2px solid rgb(150,150,150);
    border-left: 2px solid rgb(120,120,120); */
    border-right: 4px solid rgb(30,30,30);
    border-bottom: 3px solid black;
    min-width: 160px;
}

.project-directory > div:hover {
    /* background-color: rgba(40,40,40,.8); */
    cursor: pointer;
    width: 60%;
    height: 150px;
    transition-property: width, height, box-shadow;
    transition-duration: .2s;
    box-shadow: 15px 8px 5px rgba(50,50,50, 0.5);
    /* margin-top: 60px; */
}

